

import {Link} from 'react-router-dom'
import React, { Component } from 'react';
import './DropMenu.css'

//import icons
import HelpOutlineIcon from '@material-ui/icons/HelpOutline';
import CloudIcon from '@material-ui/icons/Cloud';
import CodeIcon from '@material-ui/icons/Code';
import ButtonStart from '../../../buttonStart/ButtonStart'

const linkStyle ={
    textDecoration: 'none',
    color:'#fff',
}

class DropMenu extends Component {
    state = {  }
    render() { 
        return (
            <div className="div-movile">  
                <div className="li"> 
                    <Link className="suport" style={linkStyle} > 
                        <HelpOutlineIcon className="icons-menu" /> Support

                     </Link>

                </div>
                <div className="li">
                    <Link className="resources"style={linkStyle}> 
                        <CodeIcon className="icons-menu"/> Resources

                     </Link>

                </div>
                <div className="li-start">
                        <ButtonStart/> 

                </div>
                <div className="li">
                    <Link className="morecontent" style={ linkStyle }> 
                         More content <CloudIcon className="icons-menu-cloud" />

                    </Link>

                </div>
                <div className="li">
                    <Link className="about" to="/about" style={linkStyle} > 
                        About
                    </Link>
                </div>
                <div className="li">
                    <Link className="donate" style={linkStyle}> Donate</Link>

                </div>

            <div className="separador" ></div>

        </div>
          );
    }
}
 
export default DropMenu;